@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  align-items: center;
}

.cvLink {
  text-decoration: none;
  color: white;
  text-align: center;
}

.introButton {
  font-size: 2rem;
  font-family: "Varela Round", sans-serif;
  color: #fff !important;
  text-decoration: none;
  background: #85c9e8;
  padding: 20px;
  border-radius: 40px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

.introButton:hover {
  background: #434343;
  letter-spacing: 1px;
  box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
  transition: all 0.4s ease 0s;
}

.intro {
  font-size: 5rem;
  margin: 40px;
  font-family: "Abril Fatface", cursive;
}

.contactForm {
  width: 100%;
  padding-bottom: 2rem;
}

.formInput {
  width: 75%;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  border: 1px solid #aaaaaa;
  line-height: 1.5px;
  height: 48px;
}

.formLabel {
  font-size: 2rem;
}
.formContainer {
  padding-top: 1rem;
}

.buttonContainer {
  padding-left: 9rem;
}

.submitButton {
  color: #597ad3 !important;
  text-transform: uppercase;
  background: #ffffff;
  padding: 1rem;
  border: 4px solid #597ad3 !important;
  border-radius: 6px;
  display: inline-block;
  transition: all 0.3s ease 0s;
  width: 50%;
}

.submitButton:hover {
  color: #494949 !important;
  border-radius: 50px;
  border-color: #494949 !important;
  transition: all 0.3s ease 0s;
}

.container {
  font-size: 2rem;
}

.paragraph {
  font-family: "Josefin Sans", sans-serif;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  background-color: white;
  color: #272727;
  padding: 20px;
  border-radius: 10px;
  border: 0px;
  border-color: rgb(49, 107, 173);
  border-style: solid;
  transition: 200ms;
  max-width: 380px;
}

.card:hover {
  transition: 200ms;
  border: 3px;
  border-color: rgb(49, 107, 173);
  border-style: solid;
}

.card-header {
  display: flex;
  flex-wrap: nowrap;

  justify-content: center;
}

.card-title-group {
  justify-content: center;
}

.card-title {
  flex: 0.5 1;
  font-size: large;
  margin-bottom: 0;
  margin-top: 0;
  text-align: center;
}

.card-image {
  border-radius: 10px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.card-text {
  width: 350px;
  font-size: medium;
}

.project-btn {
  display: inline-block;
  text-decoration: none;
  color: rgb(102, 179, 214);
  background-color: transparent;
  border: 1px solid rgb(102, 179, 214);
  border-radius: 100px;
  /* padding: 0.3em 1.2em; */
  margin: 5px;
  background-size: 200% 100%;
  background-image: linear-gradient(
    to right,
    transparent 50%,
    rgb(102, 179, 214) 50%
  );
  transition: background-position 0.3s cubic-bezier(0.19, 1, 0.22, 1) 0.1s,
    color 0.5s ease 0s, background-color 0.5s ease;
}

.project-btn:hover {
  color: rgba(255, 255, 255, 1);
  background-color: rgb(102, 179, 214);
  background-position: -100% 100%;
}

.footerContainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  justify-content: space-evenly;
}

.footerItem {
  font-size: 6rem;
}

.App {
  text-align: center;
}

.main-content {
  display: flex;
}

.image-container {
  margin: 40px;
}

.profile-photo {
  border-radius: 50%;
  text-align: center;

  margin: auto;
  height: 512px;
}

.profile-photo:focus,
.profile-photo:hover {
  -webkit-animation: wobble 1s 1;
          animation: wobble 1s 1;
}

@-webkit-keyframes wobble {
  25% {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
  50% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
  }
  75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes wobble {
  25% {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
  50% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
  }
  75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

